import { Dispatch, ReactNode, SetStateAction } from "react";
import {
  Button,
  Dialog,
  DialogActions,
  DialogContent,
  DialogTitle,
} from "@mui/material";

import { isBackDropOrEscapeKey } from "Utils";

type ConfirmBoxProps = {
  title?: string;
  children: ReactNode;
  open: boolean;
  setOpen: Dispatch<SetStateAction<boolean>>;
  onConfirm: () => void;
  positiveLabel?: string;
  negativeLabel?: string;
};

type PromptBoxProps = {
  title: string;
  children: ReactNode;
  open: boolean;
  setOpen: Dispatch<SetStateAction<boolean>>;
  onConfirm: () => void;
  positiveLabel?: string;
  negativeLabel?: string;
};

export const ConfirmBox = ({
  title = "Please Confirm",
  children,
  open,
  setOpen,
  onConfirm,
  positiveLabel = "Yes",
  negativeLabel = "No",
}: ConfirmBoxProps) => {
  return (
    <Dialog
      open={open}
      onClose={(_event, reason) =>
        !isBackDropOrEscapeKey(reason) && setOpen(false)
      }
      PaperProps={{ sx: { maxWidth: 320 } }}
    >
      <DialogTitle sx={{ p: 2.5 }}>{title}</DialogTitle>
      <DialogContent sx={{ color: "#24272CB3" }}>{children}</DialogContent>
      <DialogActions
        sx={{ px: 2.5, pt: 0, pb: 2.5, justifyContent: "flex-start" }}
      >
        <Button
          variant="contained"
          onClick={() => setOpen(false)}
          color="secondary"
          sx={{
            justifyContent: "center",
            height: 40,
            borderRadius: 2,
          }}
        >
          {negativeLabel}
        </Button>
        <Button
          variant="contained"
          onClick={() => {
            setOpen(false);
            onConfirm();
          }}
          color="primary"
          sx={{
            justifyContent: "center",
            height: 40,
            borderRadius: 2,
          }}
        >
          {positiveLabel}
        </Button>
      </DialogActions>
    </Dialog>
  );
};

export const PromptBox = ({
  title,
  children,
  open,
  setOpen,
  onConfirm,
  positiveLabel = "Ok",
  negativeLabel = "Cancel",
}: PromptBoxProps) => {
  return (
    <Dialog
      open={open}
      onClose={(_event, reason) =>
        !isBackDropOrEscapeKey(reason) && setOpen(false)
      }
      PaperProps={{ sx: { width: 320 } }}
    >
      <DialogTitle sx={{ p: 2.5 }}>{title}</DialogTitle>
      <DialogContent sx={{ color: "#24272CB3" }}>{children}</DialogContent>
      <DialogActions
        sx={{ px: 2.5, pt: 0, pb: 2.5, justifyContent: "flex-start" }}
      >
        <Button
          variant="contained"
          onClick={() => setOpen(false)}
          color="secondary"
          sx={{
            justifyContent: "center",
            height: 40,
            borderRadius: 2,
          }}
        >
          {negativeLabel}
        </Button>
        <Button
          variant="contained"
          onClick={() => {
            setOpen(false);
            onConfirm();
          }}
          color="primary"
          sx={{
            justifyContent: "center",
            height: 40,
            borderRadius: 2,
          }}
        >
          {positiveLabel}
        </Button>
      </DialogActions>
    </Dialog>
  );
};
