import { useEffect, useRef, useState } from "react";
import { Box, CardMedia, Link } from "@mui/material";

import { Play, Pause } from "Assets/Icons";
import { MediaWrapperSx } from "SharedComponents/CommonComponents";

type VideoProps = {
  mediaSrc: string;
};

const Video = ({ mediaSrc }: VideoProps) => {
  const videoRef = useRef<HTMLVideoElement>(null);
  const [playVideo, setPlayVideo] = useState(false);
  const [videoHover, setVideoHover] = useState(false);
  const [videoState, setVideoState] = useState("");

  useEffect(() => {
    if (videoRef.current) {
      videoRef.current.addEventListener("playing", () => {
        setVideoState("playing");
      });
      videoRef.current.addEventListener("pause", () => {
        setVideoState("");
      });
      videoRef.current.addEventListener("ended", () => {
        setVideoState("");
      });
      playVideo ? videoRef.current.play() : videoRef.current.pause();
    }
  }, [playVideo]);

  return (
    <Link
      underline="none"
      component="button"
      type="button"
      onClick={() => setPlayVideo(!playVideo)}
      onMouseEnter={() => setVideoHover(true)}
      onMouseLeave={() => setVideoHover(false)}
      sx={MediaWrapperSx}
    >
      <CardMedia
        component="video"
        ref={videoRef}
        className="video"
        disablePictureInPicture={true}
        sx={{
          objectFit: "cover",
          objectPosition: "center",
          width: "100%",
          height: "auto",
        }}
      >
        <source src={mediaSrc} type="video/mp4" />
      </CardMedia>
      <Box position="absolute" display="flex">
        {videoState === "" && <Play />}
        {videoHover && videoState === "playing" && <Pause />}
      </Box>
    </Link>
  );
};

export default Video;
