import { Dispatch, SetStateAction, useCallback, useRef } from "react";
import { Box, ClickAwayListener, Popper, PopperProps } from "@mui/material";
import { ColorFormats, ColorPicker, HEX } from "react-canvas-color-picker";

type ColorSelectorProps = {
  color: HEX;
  changeColor: Dispatch<SetStateAction<HEX>>;
  open: boolean;
  anchorEl: PopperProps["anchorEl"] | null | HTMLElement;
  doClose: () => void;
};

const ColorSelector = ({
  color,
  open,
  anchorEl,
  doClose,
  changeColor,
}: ColorSelectorProps) => {
  const formats = useRef<ColorFormats[]>(["hex"]);
  const handleChange = useCallback(({ colors }: any) => {
    changeColor(colors.hex);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  return (
    <Popper
      open={open}
      anchorEl={anchorEl}
      placement="bottom-end"
      sx={{ zIndex: 1500 }}
    >
      <Box className="wrapper" sx={{ bgcolor: "#ffffff", p: 2.5 }}>
        <ClickAwayListener onClickAway={doClose}>
          <Box className="inner">
            <ColorPicker
              spectrum="hsva"
              formats={formats.current}
              initialColor={color}
              onPanStart={handleChange}
              onPan={handleChange}
              hideAlpha={true}
            />
          </Box>
        </ClickAwayListener>
      </Box>
    </Popper>
  );
};

export default ColorSelector;
