import {
  ChangeEvent,
  FocusEvent,
  MouseEvent,
  useEffect,
  useState,
} from "react";
import {
  Button,
  Collapse,
  IconButton,
  Switch,
  Typography,
} from "@mui/material";
import { useForm } from "react-hook-form";
import moment from "moment";
import { Add, CheckCircle, Circle } from "@mui/icons-material";

import { useGlobal } from "Contexts/App";
import { useChatApi, useChatData } from "Contexts/Engage";
import {
  AddNoteStep,
  PositiveResponse,
  NegativeResponse,
  NoteStatusId,
} from "Shared/Enums/EngageLeads";
import {
  ActionArea,
  ActionButton,
  Field,
  FieldTitle,
  FlexGridColumnN,
  FlexRow,
  FormTextAreaFieldSX,
  FormTextFieldSX,
  GrowColumn,
} from "SharedComponents/CommonComponents";
import {
  AddNoteForm,
  AddNotesBody,
  AddNotesBodyPadding,
  AddNotesBox,
  AddNotesFooter,
  AddNotesHead,
  AddNotesPadding,
  AllNotesSx,
  SelectResponse,
  StatusButtonSX,
} from "Containers/Engage/Chat/LayoutComponents";
import { CallOff, CallOn, LeftArrow, WriteNote } from "Assets/Icons";
import FormTextField from "SharedComponents/FormComponents/FormTextField";
import FormDateTimeField from "SharedComponents/FormComponents/FormDateTimeField";

const AddNotes = () => {
  const { isXlScreen } = useGlobal();
  const { showAddNotes, NoteStatuses } = useChatData();
  const {
    setShowAllNotes,
    setShowAddNotes,
    setShowAllTickets,
    setShowStatusManage,
  } = useChatApi();
  const { control, handleSubmit, reset, watch, setValue } = useForm<{
    note: string;
    followUp: Date | null;
    status: NoteStatusId | string | number;
  }>({
    defaultValues: {
      note: "",
      followUp: null,
      status: NoteStatusId.INTERESTED,
    },
  });
  const { status } = watch();
  const [noteStep, setNoteStep] = useState<AddNoteStep>(
    // AddNoteStep.SELECT // always use as default
    AddNoteStep.WRITE // enable to set first for dev
  );
  const [doFollowUp, setFollowUp] = useState(false);
  const openManageStatus = () => {
    setShowStatusManage(true);
  };
  const toggleFollowUp = (event: ChangeEvent<HTMLInputElement>) => {
    setFollowUp(event.target.checked);
    setValue(
      "followUp",
      event.target.checked
        ? moment().add(1, "day").startOf("day").add(10, "hours").toDate()
        : null
    );
  };
  const onResponseClick = (event: MouseEvent<HTMLButtonElement>) => {
    setValue("note", event.currentTarget.value);
    setNoteStep(AddNoteStep.WRITE);
  };
  const setFocus = (event: FocusEvent<HTMLTextAreaElement>) => {
    const { value } = event.currentTarget;
    event.currentTarget.setSelectionRange(value.length, value.length);
  };
  const doSelectStatus = (event: MouseEvent<HTMLButtonElement>) => {
    const { value } = event.currentTarget;
    setValue("status", value);
  };
  const goBackAndReset = () => {
    setShowAddNotes(false);
    setShowAllTickets(false);
    setShowAllNotes(true);
    reset();
    setTimeout(() => setNoteStep(AddNoteStep.SELECT), 500);
  };
  const doSubmit = (data: any) => {
    console.log(JSON.stringify(data));
    setTimeout(() => goBackAndReset(), 3000);
  };

  useEffect(() => {
    if (!showAddNotes) {
      reset();
      setTimeout(() => setNoteStep(AddNoteStep.SELECT), 500);
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [showAddNotes]);

  return (
    <Collapse
      in={showAddNotes}
      orientation="horizontal"
      sx={{
        ...AllNotesSx,
        zIndex: (theme) => theme.zIndex.drawer + 2,
      }}
    >
      <AddNotesBox>
        <AddNotesHead>
          <IconButton
            onClick={() => goBackAndReset()}
            sx={{ width: 32, height: 32 }}
          >
            <LeftArrow />
          </IconButton>
          <Typography component="h4">Add Notes</Typography>
        </AddNotesHead>
        <AddNotesBody sx={AddNotesBodyPadding}>
          {(() => {
            switch (noteStep) {
              case AddNoteStep.SELECT:
                return (
                  <SelectResponse>
                    <Typography component="h3">Response of the call</Typography>
                    <Typography component="h5">Negative Response</Typography>
                    {Object.values(NegativeResponse).map(
                      (value: NegativeResponse, index: number) => (
                        <Button
                          key={index}
                          variant="text"
                          startIcon={<CallOff />}
                          className="negative"
                          value={value}
                          onClick={onResponseClick}
                        >
                          {value}
                        </Button>
                      )
                    )}
                    <Typography component="h5">Positive Response</Typography>
                    {Object.values(PositiveResponse).map(
                      (value: PositiveResponse, index: number) => (
                        <Button
                          key={index}
                          variant="text"
                          startIcon={<CallOn />}
                          className="positive"
                          value={value}
                          onClick={onResponseClick}
                        >
                          {value}
                        </Button>
                      )
                    )}
                  </SelectResponse>
                );
              case AddNoteStep.WRITE:
                return (
                  <AddNoteForm onSubmit={handleSubmit(doSubmit)}>
                    <Field>
                      <FieldTitle>Select Status</FieldTitle>
                      <FlexGridColumnN nosCol={isXlScreen ? 3 : 2}>
                        {NoteStatuses.map((NoteStatus) => (
                          <Button
                            key={NoteStatus.id}
                            value={NoteStatus.id}
                            variant="outlined"
                            className={
                              NoteStatus.id === status ? "selected" : ""
                            }
                            onClick={doSelectStatus}
                            sx={StatusButtonSX}
                          >
                            {NoteStatus.id === status ? (
                              <CheckCircle sx={{ color: NoteStatus.color }} />
                            ) : (
                              <Circle sx={{ color: NoteStatus.color }} />
                            )}
                            <span className="label">{NoteStatus.title}</span>
                          </Button>
                        ))}
                        <Button
                          variant="outlined"
                          sx={StatusButtonSX}
                          onClick={openManageStatus}
                        >
                          <Add />
                          <span className="label">Add Status</span>
                        </Button>
                      </FlexGridColumnN>
                    </Field>
                    <Field>
                      <FlexRow
                        sx={{
                          alignItems: "center",
                          justifyContent: "space-between",
                          mb: 1.75,
                        }}
                      >
                        <GrowColumn>
                          <FieldTitle sx={{ mb: 0 }}>{"Follow Up"}</FieldTitle>
                        </GrowColumn>
                        <Switch
                          checked={doFollowUp}
                          onChange={toggleFollowUp}
                          inputProps={{ "aria-label": "controlled" }}
                        />
                      </FlexRow>
                      <FormDateTimeField
                        control={control}
                        name="followUp"
                        label="Date & Time"
                        placeholder="Date & Time"
                        disabled={!doFollowUp}
                        sx={FormTextFieldSX}
                      />
                    </Field>
                    <Field>
                      <FieldTitle>Write a Note</FieldTitle>
                      <FormTextField
                        control={control}
                        name="note"
                        placeholder="Write a Note"
                        rows={5}
                        sx={FormTextAreaFieldSX}
                        inputProps={{ onFocus: setFocus }}
                        autoFocus
                        multiline
                      />
                    </Field>
                    <ActionArea justifyContent="flex-start">
                      <ActionButton
                        type="reset"
                        color="secondary"
                        sx={{ height: 48 }}
                        onClick={() => goBackAndReset()}
                        disableIcon
                      >
                        Cancel
                      </ActionButton>
                      <ActionButton type="submit" disableIcon>
                        Save Note
                      </ActionButton>
                    </ActionArea>
                  </AddNoteForm>
                );
            }
          })()}
        </AddNotesBody>
        {noteStep === AddNoteStep.SELECT && (
          <AddNotesFooter sx={AddNotesPadding}>
            <Button
              startIcon={<WriteNote />}
              children={"Write a new note"}
              variant="contained"
              value=""
              onClick={onResponseClick}
            />
          </AddNotesFooter>
        )}
      </AddNotesBox>
    </Collapse>
  );
};

export default AddNotes;
