import { Fragment } from "react";
import {
  Collapse,
  IconButton,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  Typography,
} from "@mui/material";

import { useChatData } from "Contexts/Engage";
import {
  AllTicketsBox,
  AllTicketsSx,
  TicketsActions,
  TicketsActionSX,
  TicketsBody,
  TicketsHead,
  TicketsHeadPadding,
} from "Containers/Engage/Chat/LayoutComponents";
import { FilterAltOutlined, MoreVert, Search } from "@mui/icons-material";
import { TicketStatus, TicketsType } from "Shared/Enums/EngageLeads";

interface TicketsColumn {
  id: TicketsType;
  label: string;
  minWidth?: number;
  align?: "right";
}

interface TicketsData {
  id: number;
  type: string;
  summary: string;
  status: TicketStatus;
  date: string;
}

const createData = (
  id: number,
  type: string,
  summary: string,
  status: TicketStatus,
  date: string
): TicketsData => ({ id, type, summary, status, date });

const AllTickets = () => {
  const { showAllTickets } = useChatData();
  const columns: readonly TicketsColumn[] = [
    { id: TicketsType.ID, label: "Ticket ID" },
    { id: TicketsType.TYPE, label: "Type" },
    { id: TicketsType.SUMMARY, label: "Summary" },
    { id: TicketsType.STATUS, label: "Status" },
    { id: TicketsType.DATE, label: "Date" },
  ];
  const rows = [
    createData(
      499453,
      "Complaint - Delaership",
      "Poor Response at vehicle delivery",
      TicketStatus.OPEN,
      "12 .04. 2022"
    ),
  ];

  return (
    <Fragment>
      <Collapse
        in={showAllTickets}
        orientation="horizontal"
        sx={{
          ...AllTicketsSx,
          zIndex: (theme) => theme.zIndex.drawer + 2,
        }}
      >
        <AllTicketsBox>
          <TicketsHead sx={TicketsHeadPadding}>
            <Typography component="h4">Tickets</Typography>
            <TicketsActions>
              <IconButton sx={TicketsActionSX}>
                <Search />
              </IconButton>
              <IconButton sx={TicketsActionSX}>
                <FilterAltOutlined />
              </IconButton>
              <IconButton sx={{ width: 42, height: 42 }}>
                <MoreVert />
              </IconButton>
            </TicketsActions>
          </TicketsHead>
          <TicketsBody>
            <TableContainer sx={{ maxHeight: "calc(100vh - 64px)" }}>
              <Table stickyHeader aria-label="sticky table">
                <TableHead>
                  <TableRow>
                    {columns.map((column) => (
                      <TableCell
                        key={column.id}
                        align={column.align}
                        style={{ minWidth: column.minWidth }}
                      >
                        {column.label}
                      </TableCell>
                    ))}
                  </TableRow>
                </TableHead>
                <TableBody>
                  {rows.map((row) => (
                    <TableRow
                      hover
                      key={row.id}
                      sx={{
                        "&.MuiTableRow-hover": {
                          "&:hover": {
                            bgcolor: "#F4FCFF",
                          },

                          "& .MuiTableCell-root": {
                            borderBottom: "none",
                          },
                        },
                      }}
                    >
                      {columns.map((column) => (
                        <TableCell key={column.id} align={column.align}>
                          {row[column.id]}
                        </TableCell>
                      ))}
                    </TableRow>
                  ))}
                </TableBody>
              </Table>
            </TableContainer>
          </TicketsBody>
        </AllTicketsBox>
      </Collapse>
    </Fragment>
  );
};

export default AllTickets;
