import { Fragment, useState } from "react";
import { Box, IconButton, ListItem } from "@mui/material";
import { MoreVert } from "@mui/icons-material";
import { useForm } from "react-hook-form";

import { Delete, EditO } from "Assets/Icons";
import { useSnacks } from "Contexts/Snack";
import { useChatApi } from "Contexts/Engage";
import { ConfirmBox, PromptBox } from "Components/Common/PopupBoxes";
import FormTextField from "SharedComponents/FormComponents/FormTextField";
import { FormTextFieldSX } from "SharedComponents/CommonComponents";

const NoteStatusItem = ({ NoteStatus }: any) => {
  const IconColor = "#24272C80";
  const { setSuccessMessage } = useSnacks();
  const { control, watch } = useForm({
    defaultValues: {
      id: NoteStatus.id,
      title: NoteStatus.title,
    },
  });
  const { id, title } = watch();
  const { setNoteStatuses } = useChatApi();
  const [showDelete, setShowDelete] = useState<boolean>(false);
  const [showRename, setShowRename] = useState<boolean>(false);
  const confirmDelete = async () => {
    await setNoteStatuses((prevNoteStatuses) =>
      prevNoteStatuses.filter((NoteStatus) => NoteStatus.id !== id)
    );
    setSuccessMessage("Status Successfully Removed");
  };
  const confirmRename = async () => {
    await setNoteStatuses((prevNoteStatuses) =>
      prevNoteStatuses.map((NoteStatus) =>
        NoteStatus.id === id ? { ...NoteStatus, title } : NoteStatus
      )
    );
    setSuccessMessage("Status Successfully Renamed");
  };

  return (
    <Fragment>
      <ListItem
        disablePadding
        secondaryAction={
          <Fragment>
            <IconButton onClick={() => setShowRename(true)}>
              <EditO
                sx={{
                  color: IconColor,
                }}
              />
            </IconButton>
            <IconButton onClick={() => setShowDelete(true)}>
              <Delete
                sx={{
                  color: IconColor,
                }}
              />
            </IconButton>
          </Fragment>
        }
        sx={{
          mb: 1,
          alignItems: "center",
          bgcolor: "#F2F3F7",
          borderRadius: 2,
          color: "#24272C",
          columnGap: 1.5,
          fontSize: 14,
          fontWeight: 400,
          height: 52,
          lineHeight: "16px",
          p: 1,

          "& .handle": {
            color: "#24272C4D",
            cursor: "grab",
          },

          "& span.MuiBox-root": {
            bgcolor: NoteStatus.color,
            borderRadius: 1,
            height: 24,
            minWidth: "unset",
            p: 0,
            width: 24,

            "&:hover": {
              bgcolor: NoteStatus.color,
            },
          },
        }}
      >
        <MoreVert className="handle" />
        <Box component="span" />
        {NoteStatus.title}
      </ListItem>
      <ConfirmBox
        title="Delete"
        open={showDelete}
        setOpen={setShowDelete}
        onConfirm={confirmDelete}
        positiveLabel="Delete"
        negativeLabel="Cancel"
        children={"Are you sure you want to delete this status?"}
      />
      <PromptBox
        title="Rename Status"
        open={showRename}
        setOpen={setShowRename}
        onConfirm={confirmRename}
        positiveLabel="Save"
        negativeLabel="Cancel"
        children={
          <FormTextField
            control={control}
            name={"title"}
            placeholder="Enter Status"
            variant="standard"
            required={true}
            sx={{
              ...FormTextFieldSX,
              px: 2,
            }}
          />
        }
      />
    </Fragment>
  );
};

export default NoteStatusItem;
