import { SyntheticEvent, useState } from "react";
import { HEX } from "react-canvas-color-picker";
import { useForm } from "react-hook-form";
import {
  Button,
  Dialog,
  DialogActions,
  DialogContent,
  DialogTitle,
  InputAdornment,
  Tab,
  TextField,
} from "@mui/material";
import { TabContext, TabList, TabPanel } from "@mui/lab";
import { ReactSortable } from "react-sortablejs";

import { StatusManagerTab } from "Shared/Enums/EngageLeads";
import { FlexRow, GrowColumn } from "SharedComponents/CommonComponents";
import { useSnacks } from "Contexts/Snack";
import { useChatApi, useChatData } from "Contexts/Engage";
import FormTextField from "SharedComponents/FormComponents/FormTextField";

import ColorSelector from "./ColorSelector";
import NoteStatusItem from "./NoteStatusItem";

const StatusManager = () => {
  const { setSuccessMessage } = useSnacks();
  const { showStatusManage, NoteStatuses } = useChatData();
  const { setShowStatusManage, setNoteStatuses } = useChatApi();
  const [currentTab, setCurrentTab] = useState<StatusManagerTab>(
    StatusManagerTab.NEW
  );
  const [colorButtonEl, setColorButtonEl] = useState<null | HTMLElement>(null);
  const [color, setColor] = useState<HEX>("#FF4949");
  const { control, watch } = useForm<any>({
    defaultValues: { title: "" },
  });
  const { title } = watch();
  const changeCurrentTab = (
    _event: SyntheticEvent,
    newTab: StatusManagerTab
  ) => {
    setCurrentTab(newTab);
  };
  const saveStatus = async () => {
    console.log(color, title);
    await setNoteStatuses([
      ...NoteStatuses,
      {
        id: `status-${NoteStatuses.length}`,
        color,
        title,
      },
    ]);
    setSuccessMessage("Status Successfully Created");
  };

  return (
    <TabContext value={currentTab}>
      <Dialog
        open={showStatusManage}
        onClose={() => {
          setShowStatusManage(false);
          setTimeout(() => {
            setCurrentTab(StatusManagerTab.NEW);
          }, 300);
        }}
        PaperProps={{ sx: { maxWidth: 320 } }}
        fullWidth
      >
        <DialogTitle
          component="div"
          sx={{
            borderBottom: "1px solid #24272C1A",
            py: 0,
            px: 1.5,
            mb: 2.5,
          }}
        >
          <TabList
            orientation="horizontal"
            variant="standard"
            onChange={changeCurrentTab}
            sx={{
              minHeight: "unset",

              "& .MuiTabs-flexContainer": {
                columnGap: 2.5,
                justifyContent: "center",
              },

              "& .MuiTabs-indicator": {
                height: 4,
              },

              "& .MuiTab-root": {
                color: "#24272C",
                fontSize: 18,
                fontWeight: 500,
                lineHeight: "21px",
                height: "auto",
                minHeight: "unset",
                textTransform: "capitalize",
                px: 0.5,
                py: 2,

                "&.Mui-selected": {
                  color: "inherit",
                },
              },
            }}
          >
            <Tab label="New Status" value={StatusManagerTab.NEW} />
            <Tab label="Manage Statuses" value={StatusManagerTab.MANAGE} />
          </TabList>
        </DialogTitle>
        <DialogContent sx={{ pb: 2.5, px: 1.5 }}>
          <TabPanel value={StatusManagerTab.NEW} sx={{ p: 0 }}>
            <TextField
              value={`Color: ${color}`}
              InputProps={{
                endAdornment: (
                  <InputAdornment position="end">
                    <Button
                      variant="text"
                      sx={{
                        bgcolor: color,
                        border: "1px solid #B0B0B0",
                        height: 38,
                        minWidth: "unset",
                        p: 0,
                        width: 38,
                        cursor: "pointer",

                        "&:hover": {
                          bgcolor: color,
                        },
                      }}
                      onClick={(event: React.MouseEvent<HTMLElement>) => {
                        setColorButtonEl(event.currentTarget);
                      }}
                    />
                  </InputAdornment>
                ),
                readOnly: true,
              }}
              inputProps={{
                sx: {
                  cursor: "default",
                  px: 1.5,
                },
              }}
              variant="standard"
            />
            <FormTextField
              control={control}
              name="title"
              label="Status Title"
              placeholder="Status Title"
            />
          </TabPanel>
          <TabPanel value={StatusManagerTab.MANAGE} sx={{ p: 0 }}>
            <FlexRow sx={{ mb: 1.25 }}>
              <GrowColumn sx={{ alignSelf: "center" }}>
                {`${NoteStatuses.length} Status Available`}
              </GrowColumn>
            </FlexRow>
            <ReactSortable
              list={NoteStatuses}
              setList={setNoteStatuses}
              handle=".handle"
            >
              {NoteStatuses.map((NoteStatus) => (
                <NoteStatusItem key={NoteStatus.id} {...{ NoteStatus }} />
              ))}
            </ReactSortable>
          </TabPanel>
        </DialogContent>
        {StatusManagerTab.NEW === currentTab && (
          <DialogActions
            sx={{
              borderTop: "1px solid #24272C1A",
              justifyContent: "flex-start",
            }}
          >
            <Button
              sx={{
                fontSize: 14,
                fontWeight: 500,
                height: 40,
                lineHeight: "16px",
              }}
              onClick={saveStatus}
            >
              Save Status
            </Button>
          </DialogActions>
        )}
      </Dialog>
      <ColorSelector
        open={Boolean(colorButtonEl)}
        color={color}
        anchorEl={colorButtonEl}
        doClose={() => setColorButtonEl(null)}
        changeColor={setColor}
      />
    </TabContext>
  );
};

export default StatusManager;
