import { ChangeEvent, Fragment, MouseEvent } from "react";
import {
  Box,
  Collapse,
  IconButton,
  InputAdornment,
  Typography,
} from "@mui/material";
import mime2ext from "mime2ext";

import { Dismiss, Document, Emoji, Send } from "Assets/Icons";
import { useChatApi, useChatData } from "Contexts/Engage";
import { LabeledIconButton } from "Containers/Engage/Chat/LayoutComponents";
import { formatBytes } from "Utils";
import Img from "Components/Common/Img";
import Video from "Components/Common/Video";
import FormTextField from "SharedComponents/FormComponents/FormTextField";

const SelectedAttachment = () => {
  const { chatBoxWatch, chatBoxControl } = useChatData();
  const { setChatBoxValue, setCurrentAction, setChatActionEl } = useChatApi();
  const { type, attachment, attachmentFilename, attachmentPreview } =
    chatBoxWatch();
  const docSelected = ({
    target: { files },
  }: ChangeEvent<HTMLInputElement>) => {
    if (files && files.length > 0) {
      const [file] = files;
      setChatBoxValue("type", "doc");
      setChatBoxValue("attachment", file);
      setChatBoxValue("attachmentFilename", file.name);
      setChatBoxValue("attachmentPreview", URL.createObjectURL(file));
    }
  };
  const mediaSelected = ({
    target: { files },
  }: ChangeEvent<HTMLInputElement>) => {
    if (files && files.length > 0) {
      const [file] = files;
      setChatBoxValue("type", "media");
      setChatBoxValue("attachment", file);
      setChatBoxValue("attachmentFilename", file.name);
      setChatBoxValue("attachmentPreview", URL.createObjectURL(file));
    }
  };

  return (
    <Fragment>
      <Collapse
        in={Boolean(type)}
        orientation="vertical"
        sx={{
          bgcolor: "#F7F8FA",
          position: "absolute",
          left: 0,
          bottom: 0,
          width: "100%",
          zIndex: 1,

          "& .MuiCollapse-hidden": {
            height: "0!important",
          },

          "&.MuiCollapse-entered": {
            height: "calc(100%)!important",
          },

          "& .MuiCollapse-wrapper": {
            height: "100%",
          },
        }}
      >
        <Box
          alignItems="center"
          display="flex"
          height="100%"
          justifyContent="center"
          position="relative"
          width="100%"
        >
          <input
            hidden
            id="doc-attachment"
            type="file"
            onChange={docSelected}
          />
          <input
            hidden
            accept="image/*, video/*"
            id="media-attachment"
            type="file"
            onChange={mediaSelected}
          />
          <LabeledIconButton
            color="secondary"
            disableFocusRipple
            disableRipple
            disableTouchRipple
            label="Discard Attachment"
            icon={<Dismiss sx={{ color: "#000000B3" }} />}
            variant="text"
            onClick={() => {
              setChatBoxValue("type", undefined);
              setChatBoxValue("attachment", undefined);
            }}
            sx={{
              columnGap: 2,
              position: "absolute",
              top: 20,
              right: 12,
            }}
          />
          {type === "doc" && (
            <Box
              alignItems="center"
              display="flex"
              flexDirection="column"
              justifyContent="center"
              rowGap={3}
            >
              <Document />
              {attachment && attachmentFilename && (
                <Box component="span" maxWidth={200}>
                  <Typography
                    component="h3"
                    sx={{
                      color: "#24272C",
                      fontSize: 24,
                      fontWeight: 700,
                      lineHeight: "28px",
                      mb: 1,
                      textAlign: "center",
                      wordBreak: "break-all",
                    }}
                  >
                    {attachmentFilename}
                  </Typography>
                  <Typography
                    component="h5"
                    sx={{
                      color: "#24272CB3",
                      fontSize: 18,
                      fontWeight: 700,
                      lineHeight: "21px",
                      textAlign: "center",
                    }}
                  >
                    {formatBytes(attachment.size)}
                    {" - "}
                    {mime2ext(attachment.type).toUpperCase()}
                  </Typography>
                </Box>
              )}
            </Box>
          )}
          {type === "media" && (
            <Box
              alignItems="center"
              bgcolor="#D9D9D9"
              border="1px solid #CACACA"
              boxShadow="0px 0px 30px 0px #0000001A"
              display="flex"
              height={{ xs: 400, xl: 500 }}
              justifyContent="center"
              width={{ xs: 400, xl: 500 }}
            >
              {attachment &&
                attachmentPreview &&
                attachment.type.includes("image") && (
                  <Img src={attachmentPreview} alt={attachment?.name} />
                )}
              {attachment &&
                attachmentPreview &&
                attachment.type.includes("video") && (
                  <Video mediaSrc={attachmentPreview} />
                )}
            </Box>
          )}
        </Box>
        <Box
          sx={{
            alignItems: "flex-end",
            bgcolor: "#FFFFFF",
            borderTop: "1px solid #24272C1A",
            boxShadow: "0px -8px 20px -12px #0000001A",
            bottom: 0,
            display: "flex",
            justifyContent: "space-between",
            left: 0,
            position: "absolute",
            px: 2.5,
            width: "100%",

            "& .MuiFormControl-root": {
              borderRadius: 0,
              py: 0,

              "& .MuiInputBase-root": {
                borderRadius: 0,
                py: 0,
              },
            },
          }}
        >
          {type === "doc" && (
            <FormTextField
              control={chatBoxControl}
              name="attachmentFilename"
              placeholder="Attachment Filename..."
              variant="standard"
            />
          )}
          {type === "media" && (
            <FormTextField
              control={chatBoxControl}
              name="message"
              placeholder="Write a Message..."
              maxRows={5}
              variant="standard"
              InputProps={{
                autoComplete: "off",
                startAdornment: (
                  <InputAdornment position="start" sx={{ mr: 2 }}>
                    <IconButton
                      aria-label="Clear"
                      edge="end"
                      onClick={(event: MouseEvent<HTMLElement>) => {
                        setChatActionEl(event.currentTarget);
                        setCurrentAction("emoji");
                      }}
                    >
                      <Emoji />
                    </IconButton>
                  </InputAdornment>
                ),
              }}
              multiline
            />
          )}
          <IconButton sx={{ color: "#12B0EE", mb: 0.75 }} type="submit">
            <Send sx={{ width: undefined, height: undefined }} />
          </IconButton>
        </Box>
      </Collapse>
    </Fragment>
  );
};

export default SelectedAttachment;
